"use client";

import Link from "next/link";
import { useState } from "react";
import { Menu, X } from "lucide-react";

export default function Navbar() {
  const [open, setOpen] = useState(false);

  const [isAdmin, setIsAdmin] = useState(() => {
    if (typeof window === "undefined") return false;
    return !!localStorage.getItem("admin_token");
  });

  function handleLogout() {
    if (typeof window !== "undefined") {
      localStorage.removeItem("admin_token");
      window.location.href = "/admin/login";
    }
  }

  const baseLink =
    "text-gray-700 font-serif text-lg hover:text-indigo-600 transition duration-300";

  return (
    <section className="sticky top-0 w-full bg-white shadow-md z-50">
      <nav className="max-w-7xl mx-auto px-6 py-4 flex items-center justify-between">
        
        {/* Logo */}
        <Link
          href="/"
          className="text-3xl font-bold tracking-wide text-indigo-600 font-serif"
        >
          Realestate
        </Link>

        {/* Desktop Menu */}
        <div className="hidden md:flex items-center gap-8">

          <Link href="/listing" className={baseLink}>
            Listings
          </Link>

          {!isAdmin && (
            <Link href="/admin/login" className={baseLink}>
              Admin
            </Link>
          )}

          {isAdmin && (
            <Link href="/admin/dashboard" className={baseLink}>
              Dashboard
            </Link>
          )}

          {isAdmin && (
            <button
              onClick={handleLogout}
              className="bg-indigo-600 text-white px-5 py-2 rounded-lg font-serif hover:bg-indigo-700 transition"
            >
              Logout
            </button>
          )}
        </div>

        {/* Mobile Toggle Button */}
        <button
          onClick={() => setOpen(!open)}
          className="md:hidden p-2 rounded-lg hover:bg-gray-100 transition"
        >
          {open ? <X size={26} /> : <Menu size={26} />}
        </button>
      </nav>

      {/* Mobile Dropdown */}
      {open && (
        <div className="md:hidden bg-white border-t shadow-inner">
          <ul className="flex flex-col py-4 px-6 gap-4">

            <li>
              <Link
                href="/"
                onClick={() => setOpen(false)}
                className={`${baseLink} block`}
              >
                Listings
              </Link>
            </li>

            {!isAdmin && (
              <li>
                <Link
                  href="/admin/login"
                  onClick={() => setOpen(false)}
                  className={`${baseLink} block`}
                >
                  Admin
                </Link>
              </li>
            )}

            {isAdmin && (
              <>
                <li>
                  <Link
                    href="/admin/dashboard"
                    onClick={() => setOpen(false)}
                    className={`${baseLink} block`}
                  >
                    Dashboard
                  </Link>
                </li>

                <li>
                  <button
                    onClick={handleLogout}
                    className="w-full bg-indigo-600 text-white py-2 rounded-lg font-serif hover:bg-indigo-700 transition"
                  >
                    Logout
                  </button>
                </li>
              </>
            )}
          </ul>
        </div>
      )}
    </section>
  );
}
